.App {
  width: 99vw;
  height: 99vh;
}

/*Navbar...*/

.btn-position {
  margin-left: 10%;
  width: 80%;
  margin-left: none;
  height: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.grp-name {
  margin-top: 20px;
  font-family: comic Sans Ms;
  font-weight: 800;
  font-size: 30px;
}

/*styling  of the all avilable button....*/

.btn {
  height: 60%;
  margin-top: 1.1%;
  width: 8%;
  border: 3px solid rgb(168, 7, 209);
  border-radius: 10px;

  font-family: comic Sans Ms;
}

/*styling of the hover button container.*/

#button-container {
  width: 23%;
  height: 15%;
  margin-left: 39.5%;
  margin-top: 23%;
}

.hover-btn {
  height: 100%;
  width: 100%;
  font-size: 2.5rem;
}

/*styling for the top position hover...*/

.top {
  height: 30%;
  width: 50%;
  margin-top: -50%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(202, 230, 78, 0.786);
}

/*styling for the right position hover...*/

.right {
  height: 30%;
  width: 50%;
  margin-top: -22%;
  margin-left: 105%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(208, 246, 105, 0.579);
}

/*styling for the down position hover...*/

.down {
  height: 30%;
  width: 50%;
  margin-top: 5%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(216, 249, 111, 0.784);
}

/*styling for the left position hover...*/

.left {
  height: 30%;
  width: 50%;
  margin-top: -22%;
  margin-left: -60%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(212, 243, 90, 0.946);
}

/*styling for the active  button ...*/
.active {
  background: linear-gradient(#53f4d1, #c7f174);
}
